*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  background-color: #f3f3f3;
  scroll-behavior: smooth;
  font-family: Roboto, sans-serif;
  overflow-y: scroll;
}

a {
  text-decoration: none;
  color: inherit;
}
